import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { Link } from 'react-router-dom';
import logo from './Images/logo.jpg'
import * as Dialog from '@radix-ui/react-dialog'
import { IoMdClose } from 'react-icons/io';
import Input from './Input';
import { useContext, useState } from 'react';
import { dataprovider } from './Context';
function Navbarcol() {
  const {username,setusername}=useContext(dataprovider)
  const [password,setpassword]=useState()
  return (
    <div>
      <div style={{top:"0px",left:"0px",position:"relative",width:"100%",color:"white",padding:"10px",height:"fit-content",display:"flex",backgroundColor:"#000"}}>
    
      <img src={logo} style={{height:"50px",width:"50px",borderRadius:"100%"}}></img>
      
      <div style={{padding:"15px"}}>
      <h4>Farmer Mytri</h4>
      <h4>Instant Solution</h4>
      </div>
      </div>
    <Navbar expand="lg" className="bg-body-tertiary" data-bs-theme="dark">
      <Container fluid>
        {/* <Navbar.Brand href="/">FMIS</Navbar.Brand> */}
        <center>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <Link to="/home" style={{padding:"20px",textDecoration:"none",color:"#fff"}}>Home</Link>
            <Link to="/blogs" style={{padding:"20px",textDecoration:"none",color:"#fff"}}>Blogs</Link>
            <Link to="/report" style={{padding:"20px",textDecoration:"none",color:"#fff"}}>Report</Link>
            <Link to="/" style={{padding:"20px",textDecoration:"none",color:"#fff"}}>About us</Link>
            
            <Dialog.Root  className='ml-16'>
            <Dialog.Trigger asChild>
            <button style={{padding:"20px",textDecoration:"none",color:"#fff"}}>Login</button>
            </Dialog.Trigger>
            <Dialog.Portal>
              <Dialog.Overlay
                className="
            bg-neutral-900/90 
            backdrop-blur-lg 
            fixed 
            inset-0
          "
              />
              <Dialog.Content
                className="
            fixed 
            drop-shadow-md 
            border 
           bg-white
            top-[50%] 
            left-[50%] 
            max-h-full 
            h-full 
            md:h-auto 
            md:max-h-[85vh] 
            w-full 
            md:w-[90vw] 
            md:max-w-[450px] 
            translate-x-[-50%] 
            translate-y-[-50%] 
            rounded-md 
           
            p-[25px] 
            focus:outline-none
            
          ">
                <Dialog.Title
                  className="
              text-xl 
              text-center 
              font-bold 
              text-black
              mb-4
            "
                >
                  Login
                </Dialog.Title>
                <Dialog.Description
                  className="
                  grid
              
              mb-5 
              text-sm 
              leading-normal 
              text-center
              text-white
              gap-y-3
            "
                >
                
                
                  <div className='flex flex-col gap-2 gap-x-2 w-full h-full overflow-hidden items-center justify-center content-center text-center align-middle'>
                  <Input name="email"  setnames={setusername} className="w-[300px]"></Input>
                  <Input name="password" setnames={setpassword}></Input>
                  <button className='btn btn-danger w-52'>Submit</button>
                  </div>
                 
                </Dialog.Description>

                <Dialog.Close asChild>
                  <button
                    className="
                text-neutral-600
                hover:text-black 
                absolute 
                top-[10px] 
                right-[10px] 
                inline-flex 
                h-[25px] 
                w-[25px] 
                appearance-none 
                items-center 
                justify-center 
                rounded-full 
                focus:outline-none
              "
                    aria-label="Close"
                  >
                    <IoMdClose />
                  </button>
                </Dialog.Close>
              </Dialog.Content>
            </Dialog.Portal>
          </Dialog.Root>
            
          </Nav>
          
        
        </Navbar.Collapse>
        </center>
      </Container>
    </Navbar>
    </div>
  );
}

export default Navbarcol;