import bananna from './Images/bananna.jpg'
import cotton from './Images/cotton.jpg'
import maize from './Images/maize.jpg'
import paddy from './Images/paddy.jpg'
import papaya from './Images/papaya.jpg'
import React, { Component } from 'react'

export default class Blogs extends Component {
    state={
        data:[
            {name:"bananna",
             photo:bananna
            },
            {name:"cotton",
             photo:cotton
            },
            {name:"maize",
             photo:maize
            },
            {name:"paddy",
             photo:paddy
            },
            {
                name:"papaya",
                photo:papaya
            }
        ],
        data2:[
            {name:"bananna",
             photo:bananna
            },
            {name:"cotton",
             photo:cotton
            },
            {name:"maize",
             photo:maize
            },
            {name:"paddy",
             photo:paddy
            },
            {
                name:"papaya",
                photo:papaya
            }
        ]
    }
    handleData(e){
        console.log(e.target.value)
        if(e.target.value!=""){
            const search=this.state.data.filter((value)=>{
                return value.name.indexOf(e.target.value)>-1
            })
            this.loopdata(search);
            }
            else{
                this.loopdata(this.state.data)
            }
    }
    loopdata(arr){
       this.setState({
        data2:arr,
       })
      }
  render() {
    var sample=this.state.data2.map((value)=>(
             
        <div key={value.id} className=" bg-neutral-400/5 hover:bg-neutral-400/10 rounded-lg w-33" >
        
        <img style={{borderRadius:"40px"}} className=" p-4  aspect-square rounded-2xl" fit src={value.photo} alt="Card image cap"></img>
        <div className='opacity-100 bg-black card-img-top group-hover:opacity-100'></div>
        
        <div className="card-body peer-hover:scale-110 pb-3">
          <p className="card-text text-white font-semibold">{value.name}</p>
        </div>
      </div>
    )
)
console.log(sample)
    return (
        <div className=' bg-neutral-800 gap-y-4 grid  overflow-y-auto'>
            <div>
            
                <div  className='flex gap-x-3'>                 
                <input type="text" placeholder='search' className=" w-full p-3 rounded-lg bg-neutral-500 m-3"  onChange={(e)=>this.handleData(e)}></input>
                {/* <button className='btn btn-primary'>Submit</button> */}
                </div>
                 
           
            </div>
            <div className='grid  grid-cols-4 gap-x-9 gap-y-4 items-center text-center m-3'>
         {sample}
         </div>
         <div>
            {
                sample.length===0 &&
                <div className='h-screen '>
                    <p className='text-white font-semibold '>No Search results found!</p>
             </div>
            }
         </div>
         </div>
    )
  }
}

