import React from 'react'
import { useContext } from 'react'
import { dataprovider } from './Context'
import './Profile.css'
import { useNavigate } from 'react-router-dom'
export default function Profile() {
    var navigate=useNavigate()
    const details=useContext(dataprovider)
    var changeHandler=()=>{
      navigate("/")
    }
    var submitHandler=()=>{
      
    }
  return (
    <div style={{backgroungdColor:"lightblack"}}>
        <div style={{alignContent:"center",justifyContent:"center",justifyItems:"center",marginLeft:"45%",fontFamily:"serif"}}>
      {
       details.filter((value,index)=>{
          return index%2==0&&index!=14
        }).map((value,index)=>{
          return <h4>{value}</h4>
        })
      }
      <img src={details[14]} style={{maxHeight:"300px"}}></img>
      <br></br>
      <button className='btn btn-danger' onClick={()=>{changeHandler()}}>edit</button>
      <span style={{color:"#fff"}}>!!!!</span>
      <button className='btn btn-success' onClick={()=>{submitHandler()}}>submit</button>
        </div>
      
    </div>
  )
}
